@media screen and (max-width: 522px) {
  .description-container {
    .i-love-container {
      .and {
        margin-right: 5%;
      }

      .human-friendly-text {
        margin-left: 0;
      }

      .solutions {
        margin-left: 6px;
      }
    }
  }
}

@media screen and (max-width: 448px) {
  .description-container {
    .i-love-container {
      .i-love-transforming, .and, .emotional-text,
      .human-friendly-text {
        margin-right: 5%;
      }

      .and {
        margin-right: 0;
      }

      .problems, .and, .solutions {
        margin-left: 0;
      }

      .emotional-text, .human-friendly-text {
        margin-left: 6px;
      }
    }
  }

  .footer-container {
    .footer-text {
      .challenges {
        margin-right: 5%;
      }

      .and {
        margin-left: 0;
      }

      .opportunities {
        margin-left: 6px;
      }
    }
  }
}

