  .description-container {
    .i-love-container {
      .i-love-transforming {
        margin-right: 5%;
      }

      .problems, .emotional-text {
        margin-right: 0;
      }
    }
  }

  .footer-container {
    .footer-text {
      .open-to-new {
        margin-right: 10%;
      }

      .challenges {
        margin-left: 0;
        margin-right: 0;
      }

      .and {
        margin-left: 6px;
      }

      .opportunities {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 384px) {
  .description-container {
